#tulips{
	background: url('../images/tulips_bg.jpg') no-repeat top center;
	margin-bottom: 20px;
	padding-top: 90px;
	padding-bottom: 240px;
	.title{
		font-family: 'Futura PT Extra';
		font-size: 55px;
		text-transform: uppercase;
		color: #fff;
	}
	.tulip{
		position: relative;
		.round{
			width: 657px;
			height: 657px;
			background-color: #fff;
			border-radius: 50%;
			margin: 0 auto;
			margin-top: 130px;
			position: relative;
		}
		.flower{
			width: 699px;
			height: 982px;
			background: url('../images/tulips_flower.png') no-repeat;
			position: absolute;
			bottom: -1px;
			left: -30px;
		}
		.desc{
			position: absolute;
			top: 0;
			width: 100%;
			z-index: 1;
			.item{
				position: absolute;
				display: inline-block;
				color: #fff;
				font-size: 23px;
				line-height: 35px;
				font-weight: 700;
				&.left{
					left: 0;
					&:before{
						content:'';
						display: block;
						height: 1px;
						background-color: #e5e5e5;
						width: 300px;
						position: absolute;
						left: 100%;
						top: 17px;
					}
				}
				&.right{
					right: 0;
					&:before{
						content:'';
						display: block;
						height: 1px;
						background-color: #e5e5e5;
						width: 300px;
						position: absolute;
						right: 100%;
						top: 17px;
						margin-right: 20px;
					}
				}
				&.item_1{
					top: 230px;
					width: 168px;
					&:before{
						width: 355px;
					}
				}
				&.item_2{
					top: 390px;
					width: 130px;
					&:before{
						width: 200px;
					}
				}
				&.item_3{
					top: 555px;
					width: 250px;
					&:before{
						width: 200px;
					}
				}
				&.item_4{
					top: -50px;
					width: 170px;
					right: 50px;
					&:before{
						width: 180px;
					}
				}
				&.item_5{
					top: 116px;
					width: 170px;
					right: 50px;
					&:before{
						width: 180px;
					}
				}
				&.item_6{
					top: 350px;
					width: 220px;
					&:before{
						width: 410px;
					}
				}
				&.item_7{
					top: 570px;
					width: 170px;
					right: 50px;
					&:before{
						width: 220px;
					}
				}
			}
		}
	}
	.blocks{
		display: flex;
		justify-content: space-between;
		max-width: 1140px;
		margin: 0 auto;
		margin-top: 70px;
		.item{
			width: 354px;
			height: 311px;
			border-radius: 3px;
			// background-color: #fff;
			position: relative;
			.block{
				img{
					position: absolute;
					top: 0;
					width: 100%;
				}
				.text{
					position: absolute;
					bottom: 30px;
					width: 100%;
					text-align: center;
					font-family: 'Futura PT Extra';
					font-size: 30px;
					text-transform: uppercase;
					color: #fff;
					text-shadow: 3px 3px 60px #000000;
				}
			}
			&:nth-child(1){
				.block{
					img{
					}
				}
			}
			&:nth-child(2){
				.block{
					img{
					}
				}
			}
			&:nth-child(3){
				.block{
					img{
					}
				}
			}
		}
	}
	.dost{
		margin-top: 70px;
		.block_title{
			font-family: 'Futura PT Extra';
			text-align: center;
			font-size: 34px;
			color: #fff;
