			left: -180px;
			bottom: 0;
			background: url('../images/steps_content_bg.png') no-repeat;
		}
		.step{
			position: relative;
			// width: 100%;
			flex: 0 0 440px;
			.content{
				position: absolute;
				top: 50%;
				transform: translateY(-50%);
				width: 100%;
				margin-top: -20px;
			}
			.name{
				font-family: 'Futura PT Extra';
				font-size: 24px;
				color: #000;
				margin-bottom: 30px;
			}
			.btn{
				margin-top: 30px;
				cursor: pointer;
			}
			input[type=text]{
				height: 70px;
			  border-radius: 3px;
			  border: 1px solid #e2e2e2;
			  display:block;
			  width: 100%;
			  padding-left: 35px;
			  font-family: 'Lato', sans-serif;
			  font-size: 16px;
			  margin-bottom: 20px;
			  transition: .2s;
			  max-width: 400px;
			}
			input[type=text]:last-child{
				margin-bottom: 0;
			}
			input[type=text]:focus{
			  box-shadow: 0 0 10px rgba(36, 196, 255, 0.51);
			  border: 1px solid #24c4ff;
			}
			input[name=tell]{
				margin-bottom: 0;
			}
			.txt{
				margin: 0;
				margin-top: -20px;
    			margin-bottom: 20px;
			}
