.radio_list{
	.item{
		position: relative;
		label{
			font-size: 18px;
			font-weight: 700;
			line-height: 27px;
			padding-left: 40px;
			position: relative;
			margin-bottom: 20px;
			display: inline-block;
			cursor: pointer;
			.check{
				content:'';
				display: block;
				width: 8px;
				height: 8px;
				border: 12px solid #ebebeb;
				border-radius: 50%;
				background-color: #fff;
				transition: .2s;
				position: absolute;
				left: 0px;
				top: -2px;
				&:before{
					content:'';
					display: block;
					width: 8px;
					height: 8px;
					background: #fff;
					border-radius: 50%;
					position: absolute;
					top: -4px;
					left: -4px;
				}
			}
			input{
				display: none;
				&:checked ~ .check{
					border: 12px solid #1eea8f;
				}
			}
		}
		&:last-child{
			label{
				margin-bottom: 0;
			}
		}
	}
}
